.header {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    z-index: 50;
    position: absolute;
    top: 0;
    left:0;
   

    &__container {
        display: flex;
        align-items: center;
     
    }

    &__nav {
        display: flex;
        align-items: center; 
      
    }

    &__logo-link {
        display: block;
        padding-top: 16px;
        padding-bottom: 9px;

        font-family: 'Film P3', sans-serif;
        font-weight: 700;
        font-size: 36px;
        line-height: 1.0833;
        letter-spacing: 0.3em;

        @media screen and (min-width: $tablet) {
            padding-top: 24px;
            padding-bottom: 33px;
            font-size: 40px;
            line-height: 1.075;
        }

        color: $primary-color;

    }

    &__menu {
        display: flex;
        align-items: center;

        @media screen and (min-width: $mobile) { 
            //  52,  42 -    mimiho 
            margin-left: 42px;
            gap: 20px;

        }

        //  207,  196 -    mimiho
        @media screen and (min-width: $tablet) {
            margin-left: 196px;
            gap: 72px;
        }

        //  135,  124 -    mimiho
        @media screen and (min-width: $desktop) {
            margin: 0 140px 0 124px;
            
        }
    }

    &__menu-item {
        display: flex;
        align-items: center;

        gap: 6px;

        @media screen and (min-width: $tablet) {
            gap: 32px;
        }


    }

    &__menu-link {
        padding: 28px 0 16px 0;

        font-weight: 500;
        font-size: 16px;
        line-height: 1.25;
        letter-spacing: 0.02em;

        @media screen and (min-width: $tablet) {
            padding: 38px 0 38px 0;
            font-size: 20px;
            line-height: 1.2;
        }

        color: $accent-color;
        text-decoration: none;
    }


    &__menu-link:hover,
    &__menu-link:focus {
        color: $primary-color;
    }

 
    &__phone-link {
       
        display: flex;
        align-items: center;
        gap: 4px;
        color: $accent-color;
        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

        @media screen and (min-width: $tablet) {
            width: 144px;
            height: 40px;
            border: $accent-light-color solid 1px;
            border-radius: 2px;
            padding: 7px 7px;
            transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
        }
    }

    &__phone-link:hover,
    &__phone-link:focus {
        color: $primary-color;
        @media screen and (min-width: $tablet) {
            border-color: $primary-color;
          
        }
    } 

    &__phone-icon {
        display: none;
        @media screen and (min-width: $mobile){
          display:block;
          fill: currentColor;
           margin: 28px 0 12px 0;
        }
        
        @media screen and (min-width: $tablet) {
            margin: 0;
        }
    }


    &__phone-number {
        display: none;
        white-space: nowrap;
       

        @media screen and (min-width: $tablet) {
            display: block;
            color: $primary-color;
            text-align: center;           
            font-weight: 400;
            font-size: 14px;
            line-height: 1.2143;
            letter-spacing: 0.02em;
        }
    }

    &__social-list {
        display: none;

        @media screen and (min-width: $desktop) {
            display: flex;
            align-items: center;
            gap: 20px;
        }
    }

    &__social-link {
        color: $accent-light-color;
        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    &__social-icon {
        fill: currentColor;
    }

    &__social-link:hover,
    &__social-link:focus {
        color: $primary-color;
    }
}

